@-webkit-keyframes wiggle {
  8%, 41% {
    transform: translateX(-10px);
  }
  25%, 58% {
    transform: translate(10px);
  }
  75% {
    transform: translate(-5px);
  }
  92% {
    transform: translate(5px);
  }
  0%, 100% {
    transform: translate(0);
  }
}

@keyframes wiggle {
  8%, 41% {
    transform: translateX(-10px);
  }
  25%, 58% {
    transform: translate(10px);
  }
  75% {
    transform: translate(-5px);
  }
  92% {
    transform: translate(5px);
  }
  0%, 100% {
    transform: translate(0);
  }
}
