}
#search-results-container .recent-search:hover {
  background-color: var(--gray-100);
}

#search-box {
  background-color: white;
  height: 100%;
  padding-left: 2.5rem;
  border: 1px solid var(--gray-200);
}

.search-icon {
  position: absolute;
  left: 0;
  top: 0;
  width: 2.5rem;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 1px;
}

#toggle-view {
  float: right;
}
#toggle-view .btn-primary {
  background-color: var(--gray-600);
  box-shadow: 0 0 0 0.2rem var(--gray-400);
}

.placeholder-div {
  height: 80%;
  width: -webkit-fill-available;
  padding: 50px;
  text-align: center;
  background-color: #F9FAFA;
  border-top-left-radius: calc(0.75rem - 1px);
  border-top-right-radius: calc(0.75rem - 1px);
}

.placeholder {
  font-size: 72px;
}

.filter-options {
  max-height: 300px;
  overflow: auto;
}

.address-card {
  cursor: pointer;
  position: relative;
}
.address-card .check {
  display: none;
}
.address-card.active {
  border-color: var(--primary);
}
.address-card.active .check {
  display: inline-flex;
}

.website-list .result {
  margin-top: 2rem;
}

.result {
  border-bottom: 1px solid var(--border-color);
}

.transaction-list-item {
  padding: 1rem 0;
  border-top: 1px solid var(--border-color);
  position: relative;
}
.transaction-list-item a.transaction-item-link {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-decoration: none;
  opacity: 0;
  overflow: hidden;
  text-indent: -9999px;
  z-index: 0;
}

.place-order-container {
  text-align: right;
}

.kb-card .card-body > .card-title {
  line-height: 1.3;
}